import svg4everybody from 'svg4everybody';
import $ from 'jquery';
// import './lib/jquery.magnific-popup.js';
// import './lib/slick.js';
import './lib/owl.carousel.js';
// import './lib/jquery-ui.js';
// import './lib/jquery.nicescroll.js';

$(() => {
	svg4everybody();
});

$(document).ready(function () {
	var mgLeft = $('.wrapper').css('margin-left');
	if ($('.title-bg').length) {
		console.log(mgLeft);
	}
	$('.hamburger').click(function () {
		$(this).toggleClass('is-active');
		$('.js-nav').toggleClass('is-active');
	})
	$('.top-carousel').owlCarousel({
		loop:true,
		dots: true,
		nav: true,
		navText: ['<svg class="top-carousel-left"><use xlink:href="../assets/images/icon.svg#icon_arrow-left"></use></svg>','<svg class="top-carousel-right"><use xlink:href="../assets/images/icon.svg#icon_arrow-right"></use></svg>'],
		items: 1
	});
	$(".popular-carousel").owlCarousel({
	center: true,
	margin: 0,
	dots: false,
	loop:true,
	mouseDrag: false,
	nav: true,
	navText: ['<svg class="top-carousel-left"><use xlink:href="../assets/images/icon.svg#icon_arrow-left"></use></svg>','<svg class="top-carousel-right"><use xlink:href="../assets/images/icon.svg#icon_arrow-right"></use></svg>'],
	responsive : {
		1200:{
			items:5
		},
		500: {
			items:3
		},
		0: {
			items:1
		}
	}
	});
	$(".doctors-carousel").owlCarousel({
		center: true,
		margin: 0,
		dots: false,
		loop:false,
		startPosition: 0,
		items: 3,
		mouseDrag: false,
		navText: ['<svg class="top-carousel-left"><use xlink:href="../assets/images/icon.svg#icon_arrow-left"></use></svg>','<svg class="top-carousel-right"><use xlink:href="../assets/images/icon.svg#icon_arrow-right"></use></svg>'],
		responsive : {
			767: {
				items:3
			},
			0: {
				items:1
			}
		}
	});
})



